import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Row, Col, Jumbotron, Card, Image} from "react-bootstrap"

import bookcase from "../images/bookcase.jpg"

import taco from "../images/taco.jpg"

import christmas3 from "../images/christmas2020-3.jpg"
import christmas2 from "../images/christmas2020-2.jpg"
import christmas4 from "../images/christmas2020-4.jpg"
import christmas5 from "../images/christmas2020-5.jpg"

import sidetable from "../images/sidetable.jpg"

import ginger from "../images/ginger.jpg"

import piano from "../images/piano.jpg"
import lion from "../images/lion.jpg"
import table from "../images/table.jpg"

const ArtPage = () => (
  <Layout>
    <SEO title="Artwork" />
    <Row>
      <Col><Image class="polaroid" src={bookcase} fluid/></Col>
    </Row>
    <Row>
      <Col><Image class="polaroid" src={taco} fluid/></Col>

      <Col><Image class="polaroid" src={christmas2} fluid/></Col>

      <Col><Image class="polaroid" src={christmas3} fluid/></Col>

      <Col><Image class="polaroid" src={christmas4} fluid/></Col>

      <Col><Image class="polaroid" src={christmas5} fluid/></Col>
    </Row>
    <Row>
      <Col><Image class="polaroid" src={sidetable} fluid/></Col>

      <Col><Image class="polaroid" src={ginger} fluid/></Col>

      <Col><Image class="polaroid" src={piano} fluid/></Col>

      <Col><Image class="polaroid" src={table} fluid/></Col>

      <Col><Image class="polaroid" src={lion} fluid/></Col>
    </Row>
  </Layout>
)

export default ArtPage
